<template>
  <div class="listDetail">
    <mHeader ref="child1" head-title='订单详情'></mHeader>
    <div class="detailContent">
      <div class="detailPlan">
        <span>活动计划</span>
        <ul>
          <li>
            <em>主题：</em>
            <em>生日派对</em>
          </li>
          <li>
            <em>宾客人数：</em>
            <em>5-10人</em>
          </li>
          <li>
            <em>时间：</em>
            <em>2018年10月24日</em>
          </li>
          <li>
            <em>预算范围：</em>
            <em>18k-28k</em>
          </li>
        </ul>
      </div>
      <div class="detailPlan">
        <span>活动场地</span>
        <div class="mapContent">
          <div class="map"></div>
          <p>北京市朝阳区侨福芳草地怕亨酒店</p>
        </div>
      </div>
      <div class="detailPlan">
        <span>活动信息</span>
        <div class="activeBus">
          <p>18岁</p>
        </div>
      </div>
      <div class="detailPlan">
        <span>联络人</span>
        <ul>
          <li>
            <em>王女士</em>
          </li>
          <li>
            <em>微信：</em>
            <em>5-10人</em>
          </li>
          <li>
            <em>电话：</em>
            <em>2018年10月24日</em>
          </li>
          <li>
            <em>邮箱：</em>
            <em>18k-28k</em>
          </li>
        </ul>
      </div>
      <div class="detailPlan">
        <span>订单信息</span>
        <ul>
          <li>
            <em>创建时间：</em>
            <em>2018年06月01号 </em>
          </li>
          <li>
            <em>订单号：</em>
            <em>5-10人</em>
          </li>
          <li>
            <em>已付定金：</em>
            <em>2018年10月24日</em>
          </li>
          <li class="pay">
            <em>待付尾款：</em>
            <em>18k-28k</em>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import mHeader from '../../components/header/headerTab.vue'
  export default {
    data(){
      return{

      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    methods:{
      handleScroll () {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      }
    },
    components:{
      mHeader
    },
    computed: {

    },
  }
</script>

<style lang="scss" scoped>
  @import "../../style/mixin";
  .detailContent{
    margin-top: .88rem;
    .detailPlan{
      span{
        @include fjc;
        background: rgba(22,22,17,0.7);
        @include sc(.3rem, #d3c9b3);
        height: 1.04rem;
      }
      ul{
        padding: .3rem .8rem .4rem .8rem;
        li{
          @include fjs;
          height: .42rem;
          em{
            font-style: normal;
            @include sc(.26rem, #969696);
            text-align: justify;
            display: inline-block;
          }
          em:nth-child(1){
            padding-right: .1rem;
            width: 1.4rem;
            text-align: justify;
          }
        }
        .pay{
          em{
            color: #d4be8c;
          }
        }
      }
      .mapContent{
        margin: .4rem;
        .map{
          width: 100%;
          height: 2.3rem;
          background: #999;
          margin-bottom: .1rem;
        }
        p{
          @include sc(.26rem, #969696);
          padding-left: .4rem;
        }
      }
      .activeBus{
        margin: .4rem;
        p{
          @include sc(.26rem, #969696)
        }
      }
    }
  }

</style>



